import * as React from "react";
import {Col} from 'react-bootstrap';
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";

interface IIntroProps extends React.HTMLProps<HTMLDivElement> {
  show: boolean;
  close: () => void;
}

export class Intro extends React.Component<IIntroProps> {
  public render(): JSX.Element {
    const {show, close} = this.props;
    return (
      <Modal show={show}>
        <Modal.Header closeButton>
          <Modal.Title className="text-center">Подкидной тренер</Modal.Title>
        </Modal.Header>

        <Modal.Body className="text-left">
          <p>Вы спортивный агент трех тренеров с неоднозначной репутацией. Попробуйте пристроить своих клиентов в клубы лучшей баскетбольной лиги мира.</p>
          <p><strong>Как играть:</strong> У вас три карты, на которых изображены ваши клиенты. Оппонент представляет генерального менеджера клуба, куда вы пытаетесь попасть. Попробуйте угадать, какой тренер может приглянуться руководителю. Если сделка прошла успешно, то вам начисляется очко. Игра длится три раунда.</p>
        </Modal.Body>

        <Modal.Footer>
          <Button onClick={close} variant="primary" block >Начать игру</Button>
        </Modal.Footer>
      </Modal>
    );
  }
}
