import {ActionType, getType} from "typesafe-actions";
import {drawAction, stepAction} from "./stepActionCreators";
import messages from "./messages";

export interface ICounterState {
  winCount: number;
  stepsCount: number;
  finished: boolean;
  jbb: boolean;
  message?: IMessage
}

interface IMessage {
  title: string;
  caption: string
}

const jbbMessage: IMessage = {
  title: 'БИКЕРСТАФНУЛСЯ',
  caption: 'Бикерстаф стал ГМ команды, а вам закрыты двери всех офисов лиги.'
};
const results: IMessage[] = [{
  title: 'НОЛЬ СДЕЛОК',
  caption: 'Возможно вам стоит сменить профессию? Есть свободное на почте.'
},
{
  title: 'ОДНА СДЕЛКА',
  caption: 'Вашей комиссии не хватит даже на ипотеку. '
},
{
  title: 'ДВЕ СДЕЛКИ',
  caption: 'Отличная работа. У вас определенно появилась репутация на этом рынке.'
},
{
  title: 'ТРИ СДЕЛКИ',
  caption: 'Вы просто король переговоров, доминирющий над лигой.'
}
];

const initialState: ICounterState = {
  winCount: 0,
  stepsCount: 0,
  finished: false,
  jbb: false
};

export function counterReducer(
  state: ICounterState = initialState,
  action: ActionType<any>
): ICounterState {
  switch (action.type) {
    case getType(stepAction):
      const result = messages(action.payload.opponentCard.id, action.payload.playerCard.id);
      const win = result.success ? 1 : 0;
      const jbb = action.payload.playerCard.id === 'jbb';
      return {
        ...state,
        winCount: state.winCount + win,
        stepsCount: state.stepsCount + 1,
        jbb
      };
    case getType(drawAction):
      if (state.jbb)
      {return {
        ...state,
        finished: true,
        message: jbbMessage
      };}
      else if (state.stepsCount === 3)
      {return {
        ...state,
        finished: true,
        message: results[state.winCount]
      };}
      else
      {return state}
    default:
      return state;
  }
}
